#overlay{
  height: 100vh;
  width: 100vw;
  z-index: 1;
  position: fixed;
  pointer-events: none;
}

#overlay > img {
  width: 10vh;
}

.rotatingImage {
  position: absolute;
  animation: rotate;
  animation-iteration-count: infinite;
  animation-duration: 10s;
  animation-timing-function: linear;
  width: 5%;
}

@keyframes rotate {
  0% {
    rotate: 0deg;
  }
  100% {
    rotate: 360deg;
  }
}
